#wrap-vehiculos {
  display: flex;
  flex-wrap: wrap;
  row-gap: 20px;
}
#wrap-vehiculos .wrap-listado-vehiculos,
#wrap-vehiculos .wrap-gestion-vehiculos {
  width: 100%;
}
#wrap-vehiculos .wrap-title {
  background-color: #0095e8;
  box-shadow: 0 0 7px 1px rgba(0, 0, 0, 0.32);
}
#wrap-vehiculos .wrap-title .title {
  margin: 0;
  line-height: 1;
  color: #fff;
  padding: 15px 20px;
  margin-bottom: 20px;
  text-transform: uppercase;
  font-size: 22px;
  font-weight: 700;
  text-shadow: 0px 0px 4px rgba(0, 0, 0, 0.45);
}
#wrap-vehiculos form {
  display: flex;
  flex-wrap: wrap;
  column-gap: 20px;
  row-gap: 25px;
}
#wrap-vehiculos form .wrap {
  width: 100%;
  position: relative;
}
#wrap-vehiculos form .wrap label {
  text-shadow: none;
  margin-bottom: 5px;
}
#wrap-vehiculos form .wrap a {
  color: #086ACF;
  text-decoration: underline;
  transition: all 0.5s;
}
#wrap-vehiculos form .wrap a:hover {
  color: #00b9d9;
}
#wrap-vehiculos form .wrap .select2-container .select2-selection--single {
  height: 42px;
}
#wrap-vehiculos form .wrap .select2-container .select2-selection--single .select2-selection__rendered {
  line-height: 42px;
}
#wrap-vehiculos form .wrap.wrap-2 {
  width: 49%;
}